.App {
  text-align: center;
  background-color: lightgrey;
  min-height: 100vh;
}

.app-container{
  display:flex;
  background-color: yellow;
  justify-content: center;
}
.card{
  height: 30vh;
  width: 40vh;
  background-color: white;
  color: black;
  margin: 15px;
  border: 1px solid black;
}
.green{
  font-size: 22px;
  font-weight: 600;
  color:green;
}
.red{
  font-size: 22px;
  font-weight: 600;
  color:red;
}
.countrydatacard{
  display: flex;
  flex-direction: column;
  justify-content: center;
 
 
}
.card{
  border-radius: 2em;
}
.nav-sel-md{
  display: flex;
  justify-content: center;
}
.nav-sel{
  width: 50%;
  justify-content: center;

}
tr:nth-child(odd){
  background-color:white;
}
img{
  margin-top: 10px;
  height: 200px;
  width: 200px;
}

.Aware{
  position: absolute;
 
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
 display: flex;
 justify-content: center;
 align-items: center;
}
.aware-cont{
  background-color:skyblue;
  border-radius: 2em;
  width :400px;
  height: 400px;
}
